<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-size-66">
        <edit-profile-form data-background-color="green">

        </edit-profile-form>
      </div>
      <div class="md-layout-item md-medium-size-100 md-size-33">
        <user-card :user="user">

        </user-card>
      </div>
    </div>
  </div>
</template>

<script>
import {
  EditProfileForm,
  UserCard
} from '@/pages'

export default{
  components: {
    EditProfileForm,
    UserCard
  },
  data () {
    return {
      user: {}
    }
  },
  async created () {
    let self = this
    this.$store.dispatch('setLoading', true)
    await this.$http.get('http://localhost:8848/getUser').then(data => {
      this.$store.dispatch('setLoading', false)
      if (data.body.code !== 0 || data.body.data === null) {
        this.$router.push('/login')
      } else {
        self.user = data.body.data
      }
    })
  }
}
</script>
